import React from 'react';
import CssBaseline from '@material-ui/core/CssBaseline';
import Container from '@material-ui/core/Container';
import Headers from './Headers';
import Event from './Event';
import { connect } from 'react-redux';

const sections = [
  { title: '', url: '#' },
  { title: '', url: '#' },
  { title: '', url: '#' },
  { title: '', url: '#' },
  { title: '-', url: '#' },
  { title: '', url: '#' },
  { title: '', url: '#' },
  { title: '', url: '#' },  
];

const Blog=(props)=>{

const event = {
    title: "Directum Day 2020",
    image_url: "https://izhlife.ru/uploads/posts/newsimg/imgs-1523947921.jpg",
    short_info: "3     .  . ,   ,     ,         .",
    full_info: "a",
    date: "22  2020",
    format: "b",
    where: ".   .    13  405 ",
    price: "",
    time: "9:30"
};

    //props.LK=false;
  return (
    <React.Fragment>
      <CssBaseline />
      <Container maxWidth="lg">
        <Headers sections={sections} />
        <main>
          <Event event={event} />
        </main>
      </Container>
    </React.Fragment>
  );
  
}
const mapcardStateProps = (state) => ({
  isLog: state.user.isLog,
  message: state.user.message,
  currentUser: state.user.currentUser,
  Card: state.user.Card,
})

export default connect(mapcardStateProps, {})(Blog)
